<template>
  <transition name="slide">
      <div class="add-song" @click.stop v-show="showFlag">
          <div class="header">
              <h1 class="title">添加歌曲到列表</h1>
              <div class="close" @click="hide">
                  <i class="icon-close"></i>
              </div>
          </div>
          <div class="search-box-wrapper">
              <SearchBox placeholder="搜索歌曲" @query="onQueryChange" ref="searchBox"></SearchBox>
          </div>
          <div class="shortcut" v-show="!query">
              <Switches :switches="switches" :currentIndex="currentIndex" @switch="switchItem"></Switches>
              <div class="list-wrapper">
                  <Scroll v-if="this.currentIndex === 0" :data="playHistory"
                          class="list-scroll" ref="songList">
                    <div class="list-inner">
                        <SongList :songs="playHistory" @select="selectSong">
                        </SongList>
                    </div>
                  </Scroll>
                  <Scroll v-if="this.currentIndex === 1" :data="searchHistory"
                          ref="searchList" class="list-scroll"　:refreshDelay="refreshDelay">
                    <div class="list-inner">
                        <SearchList :searches="searchHistory"  @delete="deleteOne" 
                                    @select="addQuery"></SearchList>
                    </div>
                  </Scroll>
              </div>
          </div>
          <div class="search-result" v-show="query">
              <Suggest :showSinger="showSinger" :query="query" @select="selectSuggest"
                       @listScroll="blurInput"></Suggest>
          </div>
          <TopTip ref="topTip">
            <div class="tip-title">
                <i class="icon-ok"></i>
                <span class="text">1首歌曲已经添加到播放列表</span>
            </div>
          </TopTip>
      </div>
  
  </transition>
</template>

<script>
import SearchBox from 'base/search-box/search-box'
import Suggest from 'components/suggest/suggest'
import Switches from 'base/switches/switches'
import Scroll from 'base/scroll/scroll'
import SongList from 'base/song-list/song-list'
import {searchMixin} from 'common/js/mixin'
import {mapGetters,mapActions} from 'vuex'
import Song from 'common/js/song'
import SearchList from 'base/search-list/search-list'
import TopTip from 'base/top-tip/top-tip'


export default {
    mixins: [searchMixin],
    data() {
        return {
            showFlag: false,
            showSinger: false,
            query: '',
            currentIndex: 0,
            switches: [
                {
                    name: '最近播放'
                },
                {
                    name: '搜索历史'
                }
            ]

        }
    },
    computed: {
      ...mapGetters([
        'playHistory'
      ])
    },
    methods: {
        show() {
            this.showFlag = true
            //初次进入时无法滚动（组件不显示时ｓｃｒｏｌｌ高度已经计算）
            //需要组件显示时重新渲染
            setTimeout(() => {
                if (this.currentIndex === 0) {
                    this.$refs.songList.refresh()
                } else {
                    this.$refs.searchList.refresh()
                }
        }, 20)
        },
        hide() {
            this.showFlag = false
        },
        selectSuggest() {
            this.saveSearch()
            this.$refs.topTip.show()
        },
        switchItem(index) {
            this.currentIndex = index
        },
        selectSong(song,index) {
            if(index !== 0) {
                this.insertSong(new Song(song))
                this.$refs.topTip.show()
            }
        },
        ...mapActions([
            'insertSong'
        ])
    },
    components: {
        SearchBox,
        Suggest,
        Switches,
        Scroll,
        SongList,
        SearchList,
        TopTip
    }
}
</script>




<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .add-song
    position: fixed
    top: 0
    bottom: 0
    width: 100%
    z-index: 200
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .header
      position: relative
      height: 44px
      text-align: center
      .title
        line-height: 44px
        font-size: $font-size-large
        color: $color-text
      .close
        position: absolute
        top: 0
        right: 8px
        .icon-close
          display: block
          padding: 12px
          font-size: 20px
          color: $color-theme

    .search-box-wrapper
      margin: 20px
    .shortcut
      .list-wrapper
        position: absolute
        top: 165px
        bottom: 0
        width: 100%
        .list-scroll
          height: 100%
          overflow: hidden
          .list-inner
            padding: 20px 30px
    .search-result
      position: fixed
      top: 124px
      bottom: 0
      width: 100%
    .tip-title
      text-align: center
      padding: 18px 0
      font-size: 0
      .icon-ok
        font-size: $font-size-medium
        color: $color-theme
        margin-right: 4px
      .text
        font-size: $font-size-medium
        color: $color-text
</style>


